import { render, createVNode } from 'vue'
import Confirm from '@/components/StConfirm.vue'
export interface AfterClose {
  cancel: boolean;
  confirm: boolean;
}
export interface ConfirmProps {
  title?: string; // 
  content?: string; // 
  confirmText?: string; // 
  cancelText?: string; // 
  confirm?: () => void; // 
  cancel?: () => void; // 
  afterClose?: (params: AfterClose) => void; // 
}

let container: null | HTMLElement
let options: any

const _render = (props: ConfirmProps) => {
  if (container) {
    document.body.removeChild(container)
  }
  container = document.createElement('div')
  options = {
    ...props

  }
  const vm = createVNode(Confirm, options)
  render(vm, container)
  document.body.appendChild(container)
}
// 
const show = (props: ConfirmProps) => {
  _render({
    ...props
  })
}

export default {
  show
}
